import { Component } from '@angular/core';

class CompoundFrequency {

  static readonly DAILY = new CompoundFrequency(365, 'Daily (365)');
  static readonly MONTHLY = new CompoundFrequency(12, 'Monthly (12)');
  static readonly YEARLY = new CompoundFrequency(1, 'Yearly');
  static readonly VALUES = [CompoundFrequency.DAILY, CompoundFrequency.MONTHLY, CompoundFrequency.YEARLY];

  constructor(readonly frequencyPerYear: number, readonly name: string) { }
}

class Cd {
  apy = 1;
  compoundFrequency = CompoundFrequency.DAILY;

  constructor(readonly term: number) { }

  getEffectiveApy() {
    return Math.pow(this.apy / 100 / this.compoundFrequency.frequencyPerYear + 1, this.compoundFrequency.frequencyPerYear) - 1;
  }
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  readonly cds = [new Cd(1), new Cd(2), new Cd(3), new Cd(4), new Cd(5)];
  readonly compoundFrequencies = CompoundFrequency.VALUES;
  initialInvestment = 100000;
  calculationResults: number[][] = [];

  constructor() {
    this.update();
  }

  update() {
    this.calculationResults = [];
    for (let termIndex = 0; termIndex < this.cds.length; termIndex++) {
      const termResults = [this.initialInvestment / this.cds.length];
      const cd = this.cds[termIndex];
      for (let year = 1; year <= cd.term; year++) {
        let prev = termResults[year - 1];
        if (year === cd.term && this.calculationResults.length > 0) {
          prev += this.calculationResults[termIndex - 1][year - 1];
        }
        termResults[year] = prev * (1 + cd.getEffectiveApy());
      }
      this.calculationResults.push(termResults);
    };
  }

  getAmountAtYear(cd: Cd, year: number) {
    return this.calculationResults[cd.term - 1][year];
  }
}



